
"use client";
import type { Trip, TripStatus } from "@/lib/types";
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card";
import { Separator } from "./ui/separator";
import { Button } from "./ui/button";
import { MapPin, User, Package, Clock, ShieldCheck } from "lucide-react";
import { StatusBadge } from "./status-badge";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { useState } from "react";
import { Input } from "./ui/input";
import { Alert, AlertDescription, AlertTitle } from "./ui/alert";

interface CurrentJobCardProps {
    trip: Trip;
}

const tripStatuses: TripStatus[] = ["TO_PICKUP", "AT_PICKUP", "POB", "ON_ROUTE", "AT_DROPOFF", "COMPLETED"];

export function CurrentJobCard({ trip }: CurrentJobCardProps) {
    const [currentStatus, setCurrentStatus] = useState(trip.status);
    const [otp, setOtp] = useState("");
    const [showOtpPrompt, setShowOtpPrompt] = useState(false);

    const handleStatusChange = (newStatus: TripStatus) => {
        if(newStatus === 'AT_PICKUP' || newStatus === 'COMPLETED'){
            setShowOtpPrompt(true);
        } else {
            setShowOtpPrompt(false);
            setCurrentStatus(newStatus);
        }
    }

    const handleOtpSubmit = () => {
        // Dummy OTP check
        if (otp === '1234') {
            const nextStatus = currentStatus === 'AT_PICKUP' ? 'POB' : 'COMPLETED';
            setCurrentStatus(nextStatus);
            setShowOtpPrompt(false);
            setOtp("");
        } else {
            alert("Invalid OTP");
        }
    }

    return (
        <Card>
            <CardHeader>
                <div className="flex justify-between items-start">
                    <div>
                        <CardTitle>Current Job: {trip.jobId}</CardTitle>
                        <CardDescription>Scheduled for {new Date(trip.scheduledAt).toLocaleTimeString()}</CardDescription>
                    </div>
                    <StatusBadge status={currentStatus} />
                </div>
            </CardHeader>
            <CardContent className="space-y-4">
                <div className="grid sm:grid-cols-2 gap-4">
                    <div className="flex items-start gap-3">
                        <MapPin className="h-5 w-5 text-primary mt-1" />
                        <div>
                            <p className="font-medium">From</p>
                            <p className="text-muted-foreground">{trip.pickupLocation.address}</p>
                        </div>
                    </div>
                    <div className="flex items-start gap-3">
                        <MapPin className="h-5 w-5 text-green-500 mt-1" />
                        <div>
                            <p className="font-medium">To</p>
                            <p className="text-muted-foreground">{trip.dropoffLocation.address}</p>
                        </div>
                    </div>
                </div>

                <Separator />
                
                <div className="grid sm:grid-cols-2 gap-4">
                     <div className="flex items-start gap-3">
                        <User className="h-5 w-5 text-primary mt-1" />
                        <div>
                            <p className="font-medium">Passenger</p>
                            <p className="text-muted-foreground">John Doe (Dummy)</p>
                        </div>
                    </div>
                    <div className="flex items-start gap-3">
                        <Package className="h-5 w-5 text-primary mt-1" />
                        <div>
                            <p className="font-medium">Extras</p>
                            <p className="text-muted-foreground">Child Seat, 2x Water Bottle</p>
                        </div>
                    </div>
                </div>
                
                <Button className="w-full">
                    <MapPin className="mr-2 h-4 w-4" /> Open in Maps
                </Button>

                {showOtpPrompt && (
                    <Alert>
                        <ShieldCheck className="h-4 w-4"/>
                        <AlertTitle>Confirm with Passenger</AlertTitle>
                        <AlertDescription>
                           <p className="mb-2">Please ask the passenger for the OTP to proceed.</p>
                           <div className="flex gap-2">
                             <Input 
                                type="text" 
                                placeholder="Enter 4-digit OTP" 
                                value={otp} 
                                onChange={(e) => setOtp(e.target.value)}
                                maxLength={4}
                              />
                             <Button onClick={handleOtpSubmit}>Submit</Button>
                           </div>
                        </AlertDescription>
                    </Alert>
                )}

            </CardContent>
            <CardFooter className="bg-muted/50 p-4">
                <div className="flex items-center w-full gap-4">
                    <p className="font-medium text-sm whitespace-nowrap">Update Status:</p>
                    <Select onValueChange={(value: TripStatus) => handleStatusChange(value)} defaultValue={currentStatus}>
                        <SelectTrigger className="w-full">
                            <SelectValue placeholder="Update job status" />
                        </SelectTrigger>
                        <SelectContent>
                            {tripStatuses.map(status => (
                                <SelectItem key={status} value={status}>
                                    <StatusBadge status={status} dot={false} className="border-none shadow-none" />
                                </SelectItem>
                            ))}
                        </SelectContent>
                    </Select>
                </div>
            </CardFooter>
        </Card>
    );
}
