
"use client";

import type { Driver, Trip } from "@/lib/types";
import { useState } from "react";
import { Switch } from "@/components/ui/switch";
import { Label } from "@/components/ui/label";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { DriverChecklist } from "./driver-checklist";
import { CurrentJobCard } from "./driver-current-job";
import { Separator } from "./ui/separator";

interface DriverAppClientProps {
  driver: Driver;
  trip?: Trip;
}

export function DriverAppClient({ driver, trip }: DriverAppClientProps) {
  const [isSignedOn, setIsSignedOn] = useState(driver.currentStatus !== 'OFF_DUTY');

  return (
    <div className="space-y-6">
      <div className="flex items-center space-x-2">
        <Switch 
          id="sign-on-switch" 
          checked={isSignedOn} 
          onCheckedChange={setIsSignedOn} 
          aria-readonly
        />
        <Label htmlFor="sign-on-switch" className="text-lg">
          {isSignedOn ? "On Duty" : "Off Duty"}
        </Label>
      </div>
      <Separator />

      <div className="grid md:grid-cols-3 gap-6">
        <div className="md:col-span-2">
            {trip ? (
                <CurrentJobCard trip={trip} />
            ) : (
                <Card className="flex items-center justify-center h-full">
                    <CardContent className="pt-6 text-center">
                        <p className="text-lg font-medium">No active job</p>
                        <p className="text-muted-foreground">You will be notified when a new job is assigned.</p>
                    </CardContent>
                </Card>
            )}
        </div>
        <div className="space-y-6">
          <DriverChecklist 
            title="Grooming Standards"
            items={[
              "Uniform is clean and pressed",
              "Proper identification worn",
              "Clean shaven / Tidy beard",
              "Professional demeanor"
            ]}
          />
          <DriverChecklist 
            title="Vehicle Health"
            items={[
              "Tire pressure checked",
              "Fuel level adequate",
              "Engine oil level checked",
              "Brakes and lights functional",
              "Interior is clean",
              "Exterior is clean",
              "No warning lights on dashboard",
              "Tool box checked",
              "Spare wheel available",
              "Safety triangle present"
            ]}
          />
        </div>
      </div>
    </div>
  );
}
